.contact
    
    width: 50vw
    color: black
    margin: 3rem auto
    a
        color: black
        text-decoration: none
        &:hover
            text-decoration: underline
    
    iframe
        min-width: 30vw
        min-height: 50vh
        border: none

.contact__wrap
    display: flex
    justify-content: space-evenly
    align-items: center
.contact__description
    text-align: center
    .data
        div
            cursor: pointer
            &:hover p
                        text-decoration: underline             

.work_time
    // margin-top: 15px
    .title
        font-weight: 500
        font-size: 1.4rem
        margin: 1rem
.contact__week_day
    margin-top: 5px
    margin-bottom: 5px
.contact__name
    font-weight: 500
    font-size: 1.4rem
    margin: 1rem
    
@media (max-width: 1052px)
    .contact
        width: 80vw

@media (max-width: 600px)
    .contact__wrap
        flex-direction: column