.navigation
    min-height: 15vh
    background-color: $navigationBackgroundColor
    position: relative
    .fa-bars
        position: absolute
        top: 2.4rem
        left: -5rem
        font-size: 2.4rem


.navigation__element
    text-decoration: none
    text-transform: uppercase
    font-size: 2rem
    color:$navigationTextColor
    border-bottom: 3px solid transparent
    &:hover
         border-bottom: 3px solid $navigationTextColor
         transition: 0.3s
.navigation__wrap
    min-height: 15vh
    display: flex
    justify-content: space-evenly
    align-items: center
    width: 60vw
    margin: 0 auto
.active
    border-bottom: 3px solid $navigationTextColor
    
    
@media (max-width: 900px)
    .navigation
        .fa-bars
            transition: 0.3s
            left:3rem
    .navigation__wrap
        position: absolute
        transition: 0.3s
        display: flex
        flex-direction: column
        align-items: flex-start
        text-align: left
        left: -100%
        top:15vh
        z-index: 9
    .navigation__element
        padding: 1rem
        font-size: 1.3rem
        
        
    .navigation__wrap.activeMenu
        background-color: $navigationHoverBackgroudColor   
        left: 0
        top: 15vh

@media ( max-height:400px) and (max-width: 855px)
    .navigation
        .fa-bars
            top:0.8rem
                left:4rem