.header
    background-image: url(../img/background.jpg)
    min-height: 50vh
    background-size: cover
.header__wrap
    min-height: 50vh
    background-color: rgba(200, 200, 200, 0.1)
    position: relative
    h1
        width:30vw
        position: absolute
        top:5rem
        left:3rem
        font-style: italic
        padding: 3rem
        color: $headerTextColor
        background-color: $textCackgroundColor

@media (max-width: 1052px)
    .header
        h1
            font-size: 1.4rem

@media ( max-height:400px) and (max-width: 855px)
    .header
        min-height: 70vh

@media (max-width: 600px)
    .header
        h1
            font-size: 1.2rem